<template>
  <div>
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <el-card>
      <!--  -->
      <el-row :gutter="20">
        <el-col :span="24">
          <el-form ref="formRef" :model="queryVo" label-width="80px" inline>
            <el-form-item label="" prop="majorName">
              <el-input
                v-model="queryVo.majorName"
                @keyup.enter.native="queryPage"
                placeholder=""
                clearable
                @clear="queryPage"
              ></el-input>
            </el-form-item>
            <el-form-item label="" prop="gradeYear">
              <el-input
                v-model="queryVo.gradeYear"
                @keyup.enter.native="queryPage"
                placeholder=""
                clearable
                @clear="queryPage"
              ></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" icon="el-icon-search" @click="queryPage"
                ></el-button
              >
            </el-form-item>
          </el-form>
        </el-col>
      </el-row>
      <el-row :gutter="10" class="el-row">
        <el-col :span="1.5">
          <el-button type="primary" icon="el-icon-plus"
              v-if="is_authority.is_user_add" size="mini"
          @click="(open = true)"
            ></el-button
          >
              
        </el-col>
        
        <el-col :span="1.5">
          <el-button
            type="success"
            icon="el-icon-edit"
            size="mini"
            :disabled="single"
          @click="editUser"
            v-if="is_authority.is_user_upt"
            ></el-button
          >
          
        </el-col>
        <el-col :span="1.5">
          <el-button
            type="danger"
            icon="el-icon-delete"
            size="mini"
            :disabled="multiple"
            v-if="is_authority.is_user_del"
            @click="del"
            ></el-button
          >
          
        </el-col>
        <el-col :span="1.5">
          <el-button type="info" icon="el-icon-upload2" size="mini"
            ></el-button
          >
          <!-- @click="handleImport" -->
          <!-- v-hasPermi="['system:user:import']" -->
        </el-col>
        <el-col :span="1.5">
          <el-button type="warning" icon="el-icon-download" size="mini"
            ></el-button
          >
          <!-- @click="handleExport"
                      v-hasPermi="['system:user:export']" -->
        </el-col>
        <!-- <right-toolbar :showSearch.sync="showSearch" @queryTable="getList"></right-toolbar> -->
      </el-row>

      <!--  -->
      <el-table
        :data="List"
        border
        stripe
        @selection-change="handleSelectionChange"
      >
        <el-table-column type="selection" width="50" align="center" />

        <el-table-column
          align="center"
          prop="gradeYear"
          label=""
          width="70px"
        ></el-table-column>
        <el-table-column
          align="center"
          prop="majorName"
          label=""
          width="120px"
        ></el-table-column>
        <el-table-column
          align="center"
          prop="tpName"
          label=""
          show-overflow-tooltip
        ></el-table-column>
        <el-table-column
          align="center"
          prop="coreDisciplines"
          label=""
        ></el-table-column>
        <el-table-column
          align="center"
          prop="coreCourses"
          label=""
        ></el-table-column>
        <el-table-column
          align="center"
          prop="trainingObjectives"
          label=""
        ></el-table-column>
        <el-table-column
          align="center"
          prop="graduationRequirements"
          label=""
        ></el-table-column>
        <el-table-column
          align="center"
          prop="educationAndDegree"
          label=""
        ></el-table-column>
        <el-table-column
          align="center"
          prop="tpFileFdf"
          label="PDF"
        >
        <template slot-scope="scope">
                <a :href=" scope.row.tpFileFdf">
                <img width="50px" height="50px" src="https://ss1.bdstatic.com/70cFuXSh_Q1YnxGkpoWK1HF6hhy/it/u=206184139,2148631054&fm=11&gp=0.jpg" >
                </a>
              </template>
        
        </el-table-column>
        <el-table-column
          align="center"
          prop="tpFileWord"
          label="WORD"
        >
        <template slot-scope="scope">
            <a :href="scope.row.tpFileWord">
              <img width="50px" height="50px" src="http://cdn.u1.huluxia.com/g4/M02/49/0C/rBAAdl_PeUqASp0DAAAZmCzn4uE431.png" >
            </a>
          </template>
          
          </el-table-column>
        <el-table-column
          align="center"
          prop="tpIntroduction"
          label=""     
          show-overflow-tooltip
        ></el-table-column>

        <el-table-column label="" width="120px" align="center">
          <template slot-scope="scope">
            <!--  -->
            <el-tooltip
              class="item"
              effect="dark"
              content=""
              v-if="is_authority.is_user_upt"
              placement="top-start"
              :enterable="false"
            >
              
              <el-button
                type="primary"
                icon="el-icon-edit"
                size="mini"
              @click="editUser(scope.row)"
              ></el-button>
            </el-tooltip>
            <!--  -->
            <el-tooltip
              class="item"
              effect="dark"
              content=""  v-if="is_authority.is_user_del"
              placement="top-start"
              :enterable="false"
            >
              
              <el-button
                type="danger"
                icon="el-icon-delete"
                size="mini"
                @click="del(scope.row)"
              ></el-button>
            </el-tooltip>

            
          </template>
        </el-table-column>
      </el-table>

      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="page.current"
        :page-sizes="[5, 10, 20, 30, 40]"
        :page-size="page.size"
        layout="total, sizes, prev, pager, next, jumper"
        :total="page.total"
      ></el-pagination>
      <!--  :before-close="handleClose" -->
      <!--  -->
      <el-dialog
        title=""
        :visible.sync="open"
        width="50%"
        @close="closeInit"
      >
        <!--  -->

        <el-form
          :model="Form"
          :rules="rules"
          ref="formRef"
          label-width="100px"
          class="demo-ruleForm"
        >
          <el-row :gutter="10">
            <el-col :span="12">
              <el-form-item label="" prop="gradeYear">
                <el-input
                  v-model="Form.gradeYear"
                  placeholder=""
                ></el-input>
              </el-form-item>
            </el-col>

            <el-col :span="12">
              <el-form-item label="" prop="majorId">
                <el-select v-model="Form.majorId" placeholder="" filterable>
                  <el-option
                    v-for="item in majorOptions"
                    :key="item.id"
                    :label="item.majorName"
                    :value="item.majorId">
                  </el-option>
                </el-select>
              </el-form-item>
            </el-col>
          </el-row>
          <el-form-item label="" prop="tpName">
                <el-input
                  v-model="Form.tpName"
                  placeholder=""
                ></el-input>
              </el-form-item>
          <el-row :gutter="10">
            <el-col :span="12">
              <el-form-item label="" prop="coreDisciplines">
                <el-input
                  v-model="Form.coreDisciplines"
                  placeholder=""
                ></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="" prop="coreCourses">
                <el-input
                  v-model="Form.coreCourses"
                  placeholder=""
                ></el-input>
              </el-form-item>
            </el-col>
            
          </el-row>
          <el-form-item label="" prop="trainingObjectives">
                <el-input
                  v-model="Form.trainingObjectives"
                  placeholder=""
                ></el-input>
              </el-form-item>
          <el-form-item label="" prop="graduationRequirements">
                <el-input
                  v-model="Form.graduationRequirements"
                  placeholder=""
                ></el-input>
              </el-form-item>
          <el-form-item label=""     show-overflow-tooltip prop="tpIntroduction">
                <el-input
                  v-model="Form.tpIntroduction"
                  placeholder=""
                ></el-input>
              </el-form-item>

          <el-row :gutter="10">
            <el-col :span="12">
              <el-form-item label="PDF" prop="tpFileFdf">
                <el-upload
                  :file-list="fileList"
                  class="upload-demo"
                  action="http://www.dayanhulian.com:8001/matter/fileoss/uploadOssFile"
                  multiple
                  :headers="config"
                  :limit="3"
                  :on-success="save"
                  :before-upload="beforeUpload"
                  accept=".pdf"
                >
                  <el-button icon="el-icon-upload2" size="small" type="primary"
                    ></el-button
                  >
                </el-upload>
              </el-form-item>
            </el-col>

            <el-col :span="12">
              <el-form-item label-width="120px" label="Word" prop="tpFileFdf">
                <el-upload
                  :file-list="fileList"
                  class="upload-demo"
                  action="http://www.dayanhulian.com:8001/matter/fileoss/uploadOssFile"
                  multiple
                  :headers="config"
                  :limit="3"
                  :on-success="saveWord"
                  :before-upload="beforeUploadWord"
                  accept=".doc,docx"
                >
                  <el-button icon="el-icon-upload2" size="small" type="primary"
                    ></el-button
                  >
                </el-upload>
              </el-form-item>
            </el-col>
          </el-row>

        </el-form>
        <!--  -->
        <span slot="footer" class="dialog-footer">
          <el-button @click="open = false"> </el-button>
          <el-button type="primary" @click="submitForm"> </el-button>
        </span>
      </el-dialog>
    </el-card>
  </div>
</template>
<script>
import { mapState } from 'vuex'
export default {
  data() {
    return {
      is_authority: {
        is_user_del: false,
        is_user_upt: false,
        is_user_add: false,
        is_user_add_role: false
      },
      token: window.sessionStorage.getItem('token'),
      fileList: [],
      // 
      single: true,
      // 
      multiple: true,
      List: [],
      queryVo: {
        majorName: undefined,
        page: {
          current: 1,
          size: 5
        }
      },
      page: {
        current: 1,
        size: 5,
        total: 0
      },
      ids: [],
      rules: {
        gradeYear: [
          { required: true, message: '', trigger: 'blur' }
        ],
        majorId: [
          { required: true, message: '', trigger: 'blur' }
        ],
        tpName: [
          { required: true, message: '', trigger: 'blur' }
        ],
        coreDisciplines: [
          { required: true, message: '', trigger: 'blur' }
        ],
        coreCourses: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      // subOptions: [],
      majorOptions: [],
      Form: {},
      open: false,
    }
  },
  created() {
    this.isAuthority()
    this.queryPage()
    this.getTreeselect()
  },
  computed: {
    ...mapState(['btus']),
    config() {
      return { Token: this.token }
    }
  },
  methods: {
    isAuthority() {
      var arr = this.btus.split(',')
      for (var key in this.is_authority) {
        if (arr.indexOf(key) != -1) {
          this.is_authority[key] = true
        }
      }
    },
    beforeUpload(file){
        console.log(file.type);

        const isjpg = file.type === 'application/pdf';
        if (!isjpg) {
          this.$message.error(' PDF !');
        }
        return isjpg
    },
    beforeUploadWord(file){
        console.log(file.type);

        const isjpg = file.type === 'application/msword' ;
        if (!isjpg) {
          this.$message.error(' WORD !');
        }
        return isjpg
    },
    async saveWord(response, file, fileList) {
      this.Form.tpFileWord = response.data.url
      console.log(response);
    },
    async save(response, file, fileList) {
      this.Form.tpFileFdf = response.data.url
      console.log(response);
    },

    async getTreeselect() {
      const { data: res } = await this.$http.post(
        '/majorInfo/queryMajorInfoList',
        {
          page: {
            current: 1,
            size: 999
          }
        }
      )
      // this.majorOptions = res.data.dataList.records

      const a = res.data.dataList.records
      this.majorOptions = this.unique(a)
      if (res.code != 20000) return this.$message.error(res.message)
      // console.log(this.majorOptions)
      // console.log(this.subOptions)
    },

    unique(arr) {
      const res = new Map()
      return arr.filter(arr => !res.has(arr.id) && res.set(arr.id, 1))
    },
    submitForm() {
      this.$refs.formRef.validate(async valid => {
        if (valid) {
          const { data: res } = await this.$http.post(
            '/trainingPlanInfo/saveOrUpdateTrainingPlanInfo',
            this.Form
          )
          if (res.code != 20000) return this.$message.error(res.message)
          this.open = false
          this.queryPage()
          return this.$message.success(res.message)
        }
      })
    },

    closeInit() {
      // console.log(this.Form);
      this.Form = {}
      this.$refs.formRef.resetFields()
    },

    async editUser(row) {
      this.open = true
      const id = row.id || this.ids
      console.log(id)
      const { data: res } = await this.$http.get(
        '/trainingPlanInfo/getByIdTrainingPlanInfo/' + id
      )
      if (res.code != 20000) return this.$message.error(res.message)
      console.log(res)
      this.Form = res.data.entity
    },
    del(row) {
      if (row.id) {
        var a = row.id.split(',')
      }
      const userid = a || this.ids
      console.log(userid)
      this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      })
        .then(async () => {
          await this.$http.delete(
            '/trainingPlanInfo/removeByIdTrainingPlanInfos',
            {
              data: userid
            }
          )
          // console.log(res);
          this.queryPage()
          this.List = []
        })
        .then(() => {
          this.$message({
            showClose: true,
            message: '',
            type: 'success'
          })
        })
    },
    async queryPage() {
      const { data: res } = await this.$http.post(
        '/trainingPlanInfo/queryTrainingPlanInfoList',
        this.queryVo
      )
      console.log(res.data.dataList.records)
      if (res.code != 20000) return this.$message.error(res.message)
      this.List = res.data.dataList.records
      this.page.total = res.data.dataList.total
      this.page.current = res.data.dataList.current
      this.page.size = res.data.dataList.size
      // console.log(this.List)
    },
    //
    handleSelectionChange(selection) {
      this.ids = selection.map(item => item.id)
      this.single = selection.length != 1
      this.multiple = !selection.length
    },
    //
    async uptStatus(status) {
      const { data: res } = await this.$http.put(
        `/jxyxUser/users/${status.id}/state/${status.mgState}`
      )
      if (res.code != 20000) {
        status.mgState = !status.mgState
        return this.$message.error(res.message)
      }
      return this.$message.success(res.message)
    },
    // 
    handleSizeChange(newSize) {
      this.queryVo.page.size = newSize
      this.queryPage()
    },
    //
    handleCurrentChange(newCurrent) {
      this.queryVo.page.current = newCurrent
      this.queryPage()
    }
  }
}
</script>
<style lang="less" scoped>
.el-table {
  margin-top: 15px;
  font-size: 12px;
}
.el-row {
  display: flex;
  align-items: center;
}
.el-pagination {
  margin-top: 15px;
}
</style>
